html {
  font-size: 16px;
}
.App {
  text-align: center;
  background-color: #fff;
  margin-left:0;
}
.topStripe{
  width: 100%;
  height: 30px;
  background-color:#1C202A;
  color:#fff !important;
  position: fixed;
  top: 0px;
  z-index: 1;
  margin-bottom: 30px;
}
#main{
  margin-top: 30px;
  background-color: #1C202A;
}

.firstCol{
  margin-left:0px !important;
  margin-right:0px !important;
}
h2{
  color:#1C202A;
  padding-top:25px;
  padding-bottom:15px;
}
.header-logo-container {
  position: sticky;
  top: 0;
}
.logo{
  max-width: 100%;
}
.startSection  {
  height: 100%;
  /* margin-left:20%;
  margin-right:20%; */
}

.Listing  {
  background-color: #fff;
  height: 100%;
  padding-bottom: 50px;
  padding-top: 30px;
  padding-left:20px;
  padding-right:20px;
}
.listingCard{
  width:100%;
  margin-bottom: 20px;
  
}
.listingCard .card-header{
  /* color:#C95561; */
  color:#fff;
  font-weight: 500;
  background-color: #1C202A;
}
.listingCard .card-body{
  min-height:400px;
  background-color: #f3f3f3;
}
.listingCard .card-footer{
  /* color:#C95561; */
  color:#fff;
  background-color: #1C202A;
}
.listingImage {
  float:right;
  border-radius: 50% !important;
}
.listingName{
  float:left;
  text-transform: uppercase;
  
}
.listingTitle{
  float:right;
  
}
.listingIntro{
  text-align: left;
  margin-bottom: 10px;
}
.listingDescription{
  text-align: left;
  margin-top: 30px;
}


.Services  {
  
  padding-top: 30px;
  height: 100%;
  padding-left:10%;
  padding-right:10%;
  padding-bottom: 30px;
  border:0;

}
.Services h2{
  color:#fff;
}
.serviceCard{
  width:100%;
  margin-bottom: 20px;
  background-color:#1C202A;
  
}
.serviceCard .card{
  border:none;
  text-align: left;
  
}
.serviceCard img {
  width:50px;
  float:left;
}
#Contact{
  color:#1C202A;
  padding-bottom: 140px;
  padding-top:10%;
  
}

.Contacts .col{
 background-color: rgb(0, 4, 6 , .4) ;
 color:#C95561 !important;
 border: none;
}
.Contacts .row{
  background-color: rgb(0, 4, 6 , .4) ;
  color:#C95561 !important;
  border: none;
 }
.Contacts h2{
  /* color:#C95561; */
  color:#C95561 !important;
  font-size: 1.6rem;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 10px;
  margin-bottom: 10px;
   
}


.serviceCard .card-header{
  background-color: #282828;
 color: #FFF;
 font-weight: 500;
 height:70px;
 font-size: 1.4rem;
 border-color:#C95561 ;
}
.serviceCard .card-body{
  background-color: #282828;
 color: #FFF;
 min-height: 300px;
}



/**/
/*Strip the ul of padding and list styling*/
ul {
  list-style-type:none;
  padding-inline-start: 0;
}
/*Create a horizontal list with spacing*/
li {
  display:inline-block;
  margin-right: 1px;
}


/* Button */

.btn{
  
  border:none !important;
  transition:none !important;
}
.menuBtn{
  background-color: transparent !important;
  color: #fff!important;
  border:none;
  border-radius: none;
  text-decoration: underline !important;
  
}
.menuBtn:active{
  background-color: transparent !important;
  color: #fff!important;
  border:none;
  text-decoration: underline !important;
}
.btn-outline-light {
  background-color: transparent !important;
  color: #fff!important;
  border:none;
  text-decoration: underline !important;
}

.listingCard .card{
  text-align: center;
}


